import React, { useState, useLayoutEffect, useEffect } from 'react';
import classNames from 'classnames';
import {NavLink, Link} from 'react-router-dom';
import Dropdown from './Dropdown';
import { Row, Col, Button } from 'antd';
import logo from "../images/logo.svg";
import logo_w from "../images/logo_white.svg";

const Header =  React.forwardRef((props,ref) => {  
    const [dropclass,setDropclass]=useState("");
    const [scroll,setScroll]=useState("");
    const [menuVisible, setMenuVisible] = useState(false);
    const headerClassName = classNames({
        clearfix: true,
        headerTranparent: props.transparent,
    });
    const menuDropItem_about = [
        {
            id:"",
            title:"",
            url:"/about",
            hash:"",
        },
        {
            id:"",
            title:"",
            url:"/about",
            hash:"view",
        },
        {
            id:"",
            title:"",
            url:"/about",
            hash:"team",
        },
        {
            id:"",
            title:"",
            url:"/about",
            hash:"spirits",
        },
        {
            id:"",
            title:"",
            url:"/about",
            hash:"history",
        },
    ]
    const menuDropItem_service = [
        {
            id:"",
            title:"",
            url:"/service",
            hash:"",
        },
        {
            id:"",
            title:"",
            url:"/service",
            hash:"green",
        },
        {
            id:"",
            title:"",
            url:"/service",
            hash:"green",
        },
        {
            id:"",
            title:"",
            url:"/service",
            hash:"wali",
        },
        {
            id:"",
            title:"",
            url:"/service",
            hash:"wali",
        },
        {
            id:"Light Donate",
            title:"Light Donate",
            url:"/service",
            hash:"lightdonate",
        },        
    ]
    const dropItemSet = (isview) => {
        setDropclass(isview);
    }
    useLayoutEffect(()=>{
        window.scrollTo(0, 0);
    },[])
    useEffect(()=>{
        if(props.transparent === "true"){
            window.addEventListener('scroll',()=>{      
                setScroll(window.scrollY > 0 ? "scroll" : "");
            });
        }        
    })
    const phoneClick = () => {
        setMenuVisible(!menuVisible);
    };

    const menu = [
        <ul className="header-menu" key={"menu"}>
            <li>
                <NavLink to="/">
                    
                </NavLink>
            </li>            
            <li>            
                <Dropdown view={dropItemSet} content={menuDropItem_about} title={""}/>
            </li>
            <li>
                <NavLink to="/news">
                    
                </NavLink>
            </li>
            <li>
                <Dropdown view={dropItemSet} content={menuDropItem_service} title={""}/>
            </li>
            <li>
                <NavLink to="/project">
                    
                </NavLink>
            </li>
            <li>
                <NavLink to="/contact">
                    
                </NavLink>
            </li>
            <li>
                <div className="header-rightline"></div>
                <Button href={"/"} type="link">
                    EN
                </Button>                
            </li>
        </ul>
    ];

    return (
        <>
            <header id="header" className={headerClassName + " " +  scroll + " " + dropclass} ref={ref}>
                <div className='header-bg'></div>
                <div className="header-container">
                    <Row justify="space-between">
                        <Col lg={8} md={6} sm={20} xs={20}>
                            <div className="header-logo">
                                <Link to="/" id="logo">
                                    <div className='logo-wrap'>
                                        <img src={logo} alt="logo"  className='logo'/>
                                    </div>
                                    <div className='logo-w-wrap'>
                                        <img src={logo_w} alt="logo" className='logo-w'/> 
                                    </div>                                                
                                </Link>
                            </div>                        
                        </Col>
                        <Col lg={16} md={18} sm={4} xs={4}>
                            <div
                                className={menuVisible ?'header-mobile-menu open':'header-mobile-menu'}
                                onClick={() => {
                                    phoneClick();
                                }}
                            >
                                <em />
                                <em />
                            </div>
                            { menu }
                        </Col>                    
                    </Row>
                    <div className="header-menuline"></div>   
                    <div className="header-bottomline"></div>                
                </div>   
            </header>
        </>
    );

})
export default Header;