import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import {Row, Col, Modal } from 'antd';
import { Divider } from 'antd';
import logo from "../images/logo_white.svg";
import icon_arrow from "../images/icon-arrow.svg";
export default function Footer(props){
    const [isModalVisible, setIsModalVisible] = useState(false);
    const showModal = () => {
        setIsModalVisible(true);
    };
    const handleOk = () => {
        setIsModalVisible(false);
    };
    const handleCancel = () => {
        setIsModalVisible(false);
    };
    return (
        <footer className="footer">
            <div className="container">
                <Row>
                    <Col lg={6} md={24} sm={24} xs={24}>
                        <Link to="/" className='footer-logo'><img src={logo} alt="logo" /> </Link>
                    </Col>
                    <Col lg={6} md={24} sm={24} xs={24}>                         
                    </Col>
                    <Col lg={12} md={24} sm={24} xs={24}>
                         <Row gutter={[16, 16]}>
                            <Col lg={8} md={8} sm={8} xs={24}>
                                <h6><Link to="/about"></Link></h6>
                                <Link className='footer-link' to="/about"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/about"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/about"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/about"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/about"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>

                            </Col>
                            <Col lg={8} md={8} sm={8} xs={24}>
                                <h6><Link to="/service"></Link></h6>
                                <Link className='footer-link' to="/service"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/service"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/service"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/service"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                                <Link className='footer-link' to="/service"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div>Light Donate</Link>
                                <Link className='footer-link' to="/service"><div className='footer-link-img'><img src={icon_arrow} alt="arrow"/></div></Link>
                            </Col>
                            <Col lg={8} md={8} sm={8} xs={24}>
                                <h6><Link to="/news"></Link></h6>
                                <h6><Link to="/project"></Link></h6>
                                <h6><a href='/#' onClick={(e)=>{ e.preventDefault();showModal()}}></a></h6>
                                <Modal title="" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
                                    <div>
                                       <iframe title='' src="https://electricreport.rfdme.com/" width="100%" height="540" style={{borderWidth:'0px',backgroundColor:'transparent'}}></iframe>
                                    </div>
                                </Modal>
                            </Col>
                        </Row> 
                    </Col>
                </Row>
                <Divider/>
                <div className='footer-copyright'>
                    <p> 2022 RFD Micro Electricity Co., Ltd. All rights reserved.</p>
                </div>
            </div>            
        </footer>
    )
}